import React, { useState } from "react";
import axios from "axios";
import SinglePost from "./SinglePost";
function ShowAll() {
  let [data1, setdata1] = useState(""); // it is used to store the search movie name
  let [data2, setdata2] = useState([]); // it is used to store the movie content
  let [data3, setdata3] = useState();

  function call1(e) {
    setdata1(e.target.value); // it set  the input field value in data1
  }
  async function call2() {
    if (data1 == "") {
      // if input field is empty
      alert("Please Enter Movie Name");
    } else {
      await axios
        .get("https://www.omdbapi.com/?s=" + data1 + "&apikey=597ca71a") 
        .then((res) => {
          setdata2(res.data.Search);
        });
      setdata1("");
    }
  }
  function call3(eve) {
    setdata3(eve);
  }
  return (
    <>
      <div className="box2">
        <input
          onChange={call1}
          type="text"
          value={data1}
          placeholder="Enter any movie name..."
        ></input>
        <button id="id3" onClick={call2}>
          Get Movies
        </button>
      </div>
      {/* below code means if data3 get some value so it will pass the props to the
      another component which is SinglePost and send the data3 content */}
      {data3 && <SinglePost name1={data3} name2={setdata3}></SinglePost>}

      {/* it means if data2 is having some item it will print otherwise it will
      show not found basically below syntax is like if else */}
      {data2?.length ? (
        data2.map((items) => {
          return (
            <>
              <div className="col-md-3 mb-2 mt-4">
                <div className="card shadow p-3 mb-5 bg-white rounded">
                  <img src={items.Poster} className="card-img-top"></img>
                  <div className="card-body ">
                    <h5 className="card-title">{items.Title}</h5>
                    <button
                      onClick={() => {
                        call3(items.Title);
                      }}
                      className="btn btn-info  "
                    >
                      Know More
                    </button>
                  </div>
                </div>
              </div>
            </>
          );
        })
      ) : (
        <h4 id="content-2">Not Found !</h4> // else case
      )}
    </>
  );
}
export default ShowAll;
