import axios from "axios";
import React, { useEffect, useState } from "react";
function SinglePost(props) {
  let [data, setdata] = useState([]);
  useEffect(() => {
    axios
      .get("https://www.omdbapi.com/?t=" + props.name1 + "&apikey=597ca71a")
      .then((res) => {
        setdata(res.data); // res.data means the information about movie is stored in data
      });
  }, [props]); // it is used to run use effect again that means if we click to know more
  // props get affected by which use effect run
  return (
    <>
      <div id="card" className="card flex-row">
        <img style={{ width: "50%" }} src={data.Poster} />
        <div id="card1" className="card-body">
          <div className="card-title">
            <h1> {data.Title} </h1>
            <h4>{data.Plot}</h4>
          </div>
          <strong>Actors</strong>
          <p className="card-text">{data.Actors}</p>
          <strong>Released</strong>
          <p className="card-text">{data.Released}</p>
          <strong>Genre</strong>
          <p className="card-text">{data.Genre}</p>
          <strong>Runtime</strong>
          <p className="card-text">{data.Runtime}</p>
          <strong>Type</strong>
          <p className="card-text">{data.Type}</p>
          <strong>Writer</strong>
          <p className="card-text">{data.Writer}</p>
          <strong>ImdbRating</strong>
          <p className="card-text">{data.imdbRating}</p>
          <button
            className="btn btn-danger"
            id="id4"
            onClick={() => {
              props.name2(); // means it will remove the information about the movie
            }}
          >
            Close
          </button>
        </div>
      </div>
    </>
  );
}
export default SinglePost;
